import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import projectType from 'components/__types__/project';


const ProjectFieldTable = ({ t, project, tickets }) => {
  var issues_length = "";
  if (tickets.issues) {
    issues_length = String(tickets.issues.length);
  }
  else {
    issues_length = "Loading...";
  }
  
  return(
    <div>
      <h1>JIRA Issues</h1>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>{t('common.name')}</TableCell>
            <TableCell>{t('common.value')}</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            <TableCell>
              <span>{t('entities.project.id')}</span>
            </TableCell>
            <TableCell>
              <span>{project.id}</span>
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>
              <span>{t('entities.project.name')}</span>
            </TableCell>
            <TableCell>
              <span>{project.name}</span>
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>
              Number of Issues Returned
            </TableCell>
            <TableCell>
              <span>{issues_length}</span>
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>
              JSON Data
            </TableCell>
            <TableCell>
              <span>{JSON.stringify(tickets)}</span>
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </div>
  )}
  

ProjectFieldTable.propTypes = {
  project: projectType,
  t: PropTypes.func.isRequired,
  tickets: PropTypes.object
};

ProjectFieldTable.defaultProps = {
  project: [],
};

export default withTranslation()(ProjectFieldTable);
